import React, { useEffect, useState } from 'react';
import '../src/index.css';
import ButImg from './img/send.png';
import { Button, FormControl, IconButton, Input, InputLabel } from '@material-ui/core/';
import Message from './Message';
import firebase from 'firebase';
import db from './firebase';
import FlipMove from 'react-flip-move';
import SendIcon from '@material-ui/icons/Send';

const App = () => {

    const [input, setInput] = useState('');
    const [messages, setMessages] = useState([{ username: 'Neh', message: 'Hello' }]);
    const [username, setUsername] = useState('');

    useEffect(() => {
        db.collection('messages').orderBy('timestamp', 'desc').onSnapshot(snapshot => {
            setMessages(snapshot.docs.map(doc => ({ id: doc.id, message: doc.data() })))
        });
    }, []);

    useEffect(() => {
        let name = prompt("Enter Your Name");
        if (name === "" || name === null) {
            setUsername("Unknown User");
        }
        else {
            setUsername(name);
        }
    }, []);

    const sendMessage = (event) => {
        event.preventDefault();
        db.collection('messages').add({
            message: input,
            username: username,
            timestamp: firebase.firestore.FieldValue.serverTimestamp()
        });
        setInput('');
    };

    return (
        <>
            <div className="app">
                <div className="navbar">
                    <h2>Welcome to Chat</h2>
                    <h2 className="align">{username}</h2>
                </div>
                <FlipMove>
                    {messages.map(({ id, message }) => {
                        return <Message key={id} username={username} text={message} />
                    })}
                </FlipMove>
                <form className="main-footer">
                    <FormControl className="in_footer">
                        <Input placeholder="Type a message.." className="input" value={input} onChange={event => setInput(event.target.value)} />
                        <IconButton variant="contained" color="primary" disabled={!input} type="submit" onClick={sendMessage}>
                            <SendIcon />
                        </IconButton>
                    </FormControl>
                </form>
            </div>
        </>
    );
};

export default App;