import React, { forwardRef } from 'react';
import { Card, CardContent, Typography } from "@material-ui/core";
import './Message.css';

const Message = forwardRef((props, ref) => {

    const isUser = props.username === props.text.username;

    return (
        <>
            <div ref={ref} className={`message_card ${isUser && 'message_user'}`}>
                <p className="username">{props.text.username}</p>
                <Card className={isUser ? "message_userCard" : "message_guestCard"}>
                    <CardContent>
                        <Typography variant="h6" component="h6" >{props.text.message}</Typography>
                    </CardContent>
                </Card>
            </div>
        </>
    )
});

export default Message;